@import '../../assets/fonts/ABeeZee/ABeeZee.css';

.container {
	position: fixed;
	font-family: 'ABeeZee', sans-serif;
	height: 130px;
	width: 100vw;
	padding: 2em;
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: #3e4491;
	z-index: 100;
}

.container a {
	color: white;
	font-size: 42px;
	text-decoration: none;
}

.links {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 130px;
	min-width: 420px;
}

.links a {
	font-size: 30px;
}

a {
	transition: 400ms;
}

a:hover {
	transform: translateY(4px);
}
