@import '../../../../assets/fonts/ComicNeue/ComicNeue.css';

.add-user-info-container {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 180px;
}

.add-user-info-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 180px;
	margin-right: 20px;
}

.add-name-input,
.add-email-input {
	font-family: 'Comic Neue', sans-serif;
	width: 400px;
	height: 50px;
	font-size: 26px;
	color: #b0b0b0;
	margin-left: 17px;
	margin-right: 100px;
	padding: 0.3em;
	border: 3px solid #d9d9d9;
	transition: 400ms;
}

.add-email-input {
	width: 450px;
	height: 40px;
	font-size: 22px;
	margin-top: 10px;
}

.generate-avatar {
	height: 180px;
	width: 180px;
	border: 4px solid white;
	background-color: rgb(135, 207, 235);
	display: flex;
	text-align: center;
	align-items: center;
	justify-content: center;
	padding: 1em;
	cursor: pointer;
}

.generated-avatar {
	height: 180px;
	width: 180px;
	border: 4px solid white;
	animation-duration: 650ms;
	animation-name: avatarAppear;
}

@keyframes avatarAppear {
	from {
		transform: scale(0.7);
	}

	to {
		transform: scale(1);
	}
}

#generate-avatar-title {
	color: black;
	font-weight: bold;
	font-size: 20px;
}

.add-name-input:focus,
.add-email-input:focus {
	color: black;
}

.add-name-input:hover,
.add-email-input:hover {
	transform: translateY(1px);
	box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.448);
}
